* {
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
    box-sizing: border-box;
}

.body{
    background-color: #fff;
}

main {
    padding: 50px 20px;
}

.container-limite {
    max-width: 1024px;
    margin: 20px auto;
}

.pesquisa-filtro{
    width: 100%;
    margin: 20px auto;
}

.container-botoes {
    margin: 20px auto;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    
}


.container-botoes img {
    width: 20px;
    height: 20px;
}

.container-botoes button {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    padding: 5px 20px;
    border: none;
    border-radius: 20px;
    gap: 10px;
}

button p{
    font-size: 15px;
}

button:hover {
    background-color: #daa520;
    cursor: pointer;
    box-shadow: 1px  1px 5px #000;
}

.container-busca img{
    width: 20px;
}

.container-busca {
    display: flex;
    align-items: center;
    border-radius: 20px;
    gap: 5px;
    background-color: #f3f3f3;
    width: 100%;
    padding: 5px 10px;
}

.container-busca input {
    border: none;
    cursor: pointer;
    width: 95%;
    padding: 5px 15px;
    border-radius: 50px;
}

h2 {
    font-size: 30px;
    margin: 20px 0;
    font-family: 'Dancing Script', cursive;
    text-align: center;
    color: #daa520;
}

.container-cards{
    width: 100%;
    padding: 20px;
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    background-color: #f3f3f3;
    
}

